	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2006-11-08-meet-ning-marc-andreessen-gina-bianchini/index.md absPath of file >>> MarkdownRemark","html":"<p>Empower people to create their own social sites. I played with the beta a while back and it seemed to be decent. I believe you can download your source code from their hosted service and deploy it elsewhere.\nThey are showing that it lets you store your own pictures, video, do your own blog, etc.</p>\n<p>Ning has full RSS and a REST api. You can reprogram it or add features to it. You can hit the view source button at anytime and then customize the code.</p>\n<p>They seem to be betting that the ability to customize the social network will appeal to users.</p>","frontmatter":{"title":"Meet Ning &#8212; Marc Andreessen , Gina Bianchini","date":"November 08, 2006","imageUrl":null,"image":null}}},"pathContext":{"path":"/meet-ning-marc-andreessen-gina-bianchini"}}

